import { AjaxErrorHandler } from '../components/AjaxErrorHandler';
import { Action } from './Action';
import EventUtil from './../utils/EventUtil';
import { ShowUI } from './../components/ShowUI';

export class ShowAction extends Action {


    constructor(config, tableUI) {
        super(config); 
        this.config = config;
        this.tableUI = tableUI; 
       
    }


    init(){
        this.handleShowEntity()
    }
    /**
     * Affiche les dtails d'une entit dans un modal.
     * @param {number|string} id - Identifiant de l'entit  afficher.
     */
    showEntity(id) {


        let showUrl = this.getUrlWithId(this.config.showUrl, id); // Gnrer l'URL dynamique

        showUrl = this.appendParamsToUrl(
            showUrl,
            this.viewStateService.getContextParams()
        );



        // Afficher le chargement dans le modal
        this.tableUI.indexUI.modalUI.showLoading('Chargement...');

        // Charger les dtails de l'entit via AJAX
        $.get(showUrl)
            .done((html) => {
                // Injecter le contenu des dtails dans le modal


                this.tableUI.indexUI.modalUI.showContent(html);

                this.executeScripts(html);
                this.tableUI.indexUI.modalUI.setTitle(window.modalTitle);
                this.tableUI.indexUI.showUI.init();
     
            })
            .fail((xhr) => {
                this.tableUI.indexUI.modalUI.close();
                AjaxErrorHandler.handleError(xhr, 'Erreur lors du chargement des dtails de l\'entit.');
            });
            
    }


    /**
     * Gre les vnements lis  l'affichage des dtails d'une entit.
     */
    handleShowEntity() {
        EventUtil.bindEvent('click', `${this.config.crudSelector} .showEntity`, (e) => {
            e.preventDefault();
            const id = $(e.currentTarget).data('id'); // Rcuprer l'ID de l'entit
            this.showEntity(id);
        });
    }



}
