import { AjaxErrorHandler } from '../components/AjaxErrorHandler';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { NotificationHandler } from '../components/NotificationHandler';
import { BaseAction } from './BaseAction';
import EventUtil from '../utils/EventUtil';
import ArrayUtil from './../utils/ArrayUtil';

export class LoadListAction extends BaseAction {
    /**
     * Constructeur de LoadListAction.
     * @param {Object} config - Configuration pour le chargement des entits.
     */
    constructor(config, tableUI) {

        super(config);
        
        this.config = config;
        this.tableUI = tableUI;
        this.indexUrl = config.indexUrl;

        /**
         * Compteur incrmental pour identifier les requtes.
         * Permet de distinguer la dernire requte envoye
         * et dignorer les rponses "en retard".
         *
         *  Problme :
         * Si deux appels AJAX sont lancs presque en mme temps
         * (par exemple un auto-refresh discret et une recherche utilisateur),
         * il se peut que la premire requte (ancienne) termine APRES la seconde.
         * Rsultat  lUI serait crase par des donnes obsoltes.
         *
         *  Solution :
         * On associe un ID unique  chaque requte et
         * on napplique les rsultats QUE si cest la requte la plus rcente.
         */
        this.lastRequestId = 0;
    }

    /**
     * Charge les entits depuis le serveur et met  jour la table ou la liste.
     * @param {number} page - Numro de la page  charger (par dfaut : 1).
     * @param {Object} filters - Objets contenant les filtres actifs.
     */
    loadEntities(page, filters = {}, discret = false) {


        // Ne pas loadEntities si une cille en edition
        if(this.tableUI.inlineEdit.activeCell != null || this.tableUI.bulkAction.isSelectingRows  ) return;

        if(page === undefined){
            page = this.tableUI.indexUI.paginationUI.page;
        }
        const pageString = new URLSearchParams({page : page}).toString();
    
        let indexUrl = this.indexUrl;
        indexUrl = this.appendParamsToUrl(
            indexUrl,
            pageString,
        );

        indexUrl = this.appendParamsToUrl(
            indexUrl,
            this.viewStateService.getContextParams()
        );


        // Afficher l'indicateur de chargement
        if (!discret) {
            this.loader.showNomBloquante();
        }

        //  incrmenter l'id de la requte
        const requestId = ++this.lastRequestId;

        // Requte AJAX pour charger les donnes
        return $.get(indexUrl)
            .done((html) => {

                /**
                 *  Problme possible :
                 * Cette rponse correspond peut-tre  une requte plus ancienne
                 * (envoye avant mais arrive aprs).
                 *
                 *  Solution :
                 * Vrifier que cette rponse correspond bien  la dernire requte envoye.
                 * Sinon, on lignore pour viter dcraser lUI avec des donnes primes.
                 */
                if (requestId !== this.lastRequestId) {
                    console.debug(" Rponse ignore (requte obsolte)", { requestId, last: this.lastRequestId });
                    return;
                }

               

                // TODO :  mettre dans this.config
                const view_type = this.config.viewStateService.getVariable(this.config.view_type_variable) || "table";
                if(view_type == "widgets"){
                    $(this.config.dataContainerSelector).html("");
                    $(this.config.dataContainerOutSelector).html(html);
                }else{
                    $(this.config.dataContainerSelector).html(html);
                    $(this.config.dataContainerOutSelector).html("");
                }
            
                if (!discret) {
                    this.loader.hide();
                }
                this.executeScripts(html);

                
                this.tableUI.init();
                if (!discret) {
                    this.tableUI.indexUI.filterUI.init();
                }

            

                this.tableUI.indexUI.bulkActionsUI.init();
                this.tableUI.indexUI.notificationUI.init();
                // Afficher un message de succs (optionnel)
                // NotificationHandler.showSuccess('Donnes charges avec succs.');

                if(this.config.data_calcul && this.config.isMany && this.config.managerInstance.parent_manager){
                    this.config.managerInstance.parent_manager.formUI.reloadData();
                }
              
                
                    
            })
            .fail((xhr) => {
                AjaxErrorHandler.handleError(xhr, "Erreur lors du chargement des donnes.");
            })
            .always(() => {
                // Masquer l'indicateur de chargement
               
            });
    }
    
}
