import { ViewStateService } from "../components/ViewStateService";
import { FormUI } from "../components/FormUI";
import { LoadingIndicator } from "../components/LoadingIndicator";
import { ModalUI } from "../components/ModalUI";
import EventUtil from '../utils/EventUtil';
export class BaseAction {


    constructor(config){
        this.config = config;
        // Table Loader
        this.loader = new LoadingIndicator(config.tableSelector);
        this.loader_traitement = new LoadingIndicator("body");
        this.viewStateService = this.config.viewStateService;
        this.SuscesMessage = "";
    }
    /**
     * Gnre une URL dynamique en remplaant :id par un identifiant spcifique.
     * @param {string} baseUrl - URL de base.
     * @param {number|string} id - Identifiant  insrer.
     * @returns {string} URL complte avec l'identifiant.
     */
    getUrlWithId(baseUrl, id) {
        return baseUrl.replace(':id', id);
    }

      /**
     * Ajoute des paramtres  une URL.
     * @param {String} url - L'URL de base.
     * @param {String} params - Chane de paramtres  ajouter.
     * @returns {String} - L'URL avec les paramtres ajouts.
     */
      appendParamsToUrl(url, params) {
        if (!params) {
            return url;
        }

        
        const separator = url.includes('?') ? '&' : '?';
        return `${url}${separator}${params}`;
    }


      /**
     * Excute les scripts inclus dans le contenu HTML charg via AJAX.
     * @param {string} html - Contenu HTML contenant potentiellement des scripts.
     */
      executeScripts(html) {
        const scriptTags = $("<div>").html(html).find("script");

        scriptTags.each(function () {
            const scriptText = $(this).text();
            const scriptSrc = $(this).attr("src");

            if (scriptSrc) {
                // Charger et excuter les scripts externes
                $.getScript(scriptSrc);
            } else if (scriptText) {
                // Excuter les scripts inline
                new Function(scriptText)();
            }
        });
    }

 

}