import { BaseAction } from './BaseAction';
import EventUtil from '../utils/EventUtil';
import { NotificationHandler } from '../components/NotificationHandler';
import { AjaxErrorHandler } from '../components/AjaxErrorHandler';
import { CrudAction } from './CrudAction';

export class BulkAction extends CrudAction {

    constructor(config, tableUI) {
         super(config,tableUI);
        this.config = config;
        this.tableUI = tableUI;
        this.successMessage = 'Action en masse effectue avec succs.';
        this.isSelectingRows = false;
    }

    init() {
        this.bindRowSelectionWatcher();
        this.bindBulkActionButton();
    }

    /**
     * Surveille les changements de slection dans les lignes.
     */
    bindRowSelectionWatcher() {

        // let parent = this;
        // $(document).on('change', `#realisationTache-data-container .check-row`, function() {

        //     const selectedCount = parent.getSelectedIds().length;
        //     parent.isSelectingRows = selectedCount > 0;
        //     alert(selectedCount);
        //     console.log("isSelectingRows :", parent.isSelectingRows, "Total :", selectedCount);


        // });

        EventUtil.bindEvent('change', `${this.config.tableSelector} .check-row`, () => {
            const selectedCount = this.getSelectedIds().length;
            this.isSelectingRows = selectedCount > 0;
            console.log(selectedCount);
            console.log("isSelectingRows :", this.isSelectingRows, "Total :", selectedCount);
        });
    }

    /**
     * Initialise l'vnement sur les boutons bulk-action.
     */
    bindBulkActionButton() {
        EventUtil.bindEvent('click', `${this.config.crudSelector} .bulkActionButton`, (e) => {
            e.preventDefault();

            const button = e.currentTarget;
            const url = button.dataset.url;
            const method = button.dataset.method || 'POST';
            const confirmText = button.dataset.confirm || null;
            const actionType = button.dataset.actionType || 'ajax';

            const selectedIds = this.getSelectedIds();

            // pour permttre mettre  jour TableUI
            this.isSelectingRows = false;

            const selectedCount = selectedIds.length;

            if (selectedCount === 0) {
                // Confussion avec edit note il affiche le message on click entrer dans la note
                // in table
                // NotificationHandler.showError('Veuillez slectionner au moins une ligne.');
                return;
            }

            const doAction = () => {
                if (actionType === 'modal') {
                    // Affichage d'un formulaire d'dition en masse dans une modale
                    const finalUrl = this.appendParamsToUrl(url, this.viewStateService.getContextParams());

                    this.tableUI.indexUI.modalUI.showLoading('Modification en masse...');

                    $.get(finalUrl, { ids: selectedIds })
                        .done((html) => {
                            this.tableUI.indexUI.modalUI.showContent(html);
                            this.executeScripts(html);
                         
                            this.tableUI.indexUI.modalUI.setTitle(window.modalTitle);
                            this.tableUI.indexUI.formUI.init(() => this.submitEntity(), false);
                            this.tableUI.indexUI.formUI.disableRequiredAttributes();

                            // Active la checkbox correspondante  tout champ modifi
                            EventUtil.bindEvent('change', `${this.config.formSelector} input, ${this.config.formSelector} select, ${this.config.formSelector} textarea`, (event) => {
                                const input = event.currentTarget;
                                const fieldName = input.name?.replace('[]', '');

                                // Si le champ a un nom et une case  cocher correspondante
                                if (fieldName) {
                                    const checkbox = document.getElementById(`bulk_field_${fieldName}`);
                                    if (checkbox) {
                                        checkbox.checked = true;
                                    }
                                }
                            });

                        })
                        .fail((xhr) => {
                            this.tableUI.indexUI.modalUI.close();
                            AjaxErrorHandler.handleError(xhr, 'Erreur lors du chargement du formulaire.');
                        });
                } else {
                    // Traitement AJAX classique
                    NotificationHandler.showToast('info', `Traitement de ${selectedCount} lment(s) en cours...`);

                    $.ajax({
                        url: this.appendParamsToUrl(url, this.viewStateService.getContextParams()),
                        method: method,
                        data: {
                            ids: selectedIds,
                            _token: this.config.csrfToken
                        }
                    })
                        .done((data) => {
                            NotificationHandler.show(data.type, data.title, data.message || `${selectedCount} lment(s) trait(s).`);
                            this.tableUI.loadListAction.loadEntities();
                        })
                        .fail((xhr) => {
                            AjaxErrorHandler.handleError(xhr, 'Erreur lors de l\'action en masse.');
                        });
                }
            };

            if (confirmText) {
                NotificationHandler.confirmAction('Confirmation', `${confirmText} (${selectedCount} lment(s))`, doAction);
            } else {
                doAction();
            }
        });
    }

    /**
     * Envoie le formulaire en AJAX depuis le formulaire en masse.
     */
    submitEntity(onSuccess) {
        const form = $(this.config.formSelector);
        const actionUrl = form.attr('action');
        const method = form.find('input[name="_method"]').val() || 'POST';
        const formData = form.serialize();

        this.isSelectingRows = false;
        this.tableUI.indexUI.formUI.loader.show();

        if (!this.tableUI.indexUI.formUI.validateForm()) {
            NotificationHandler.showError('Validation choue. Veuillez corriger les erreurs.');
            this.tableUI.indexUI.formUI.loader.hide();
            return;
        }

        $.ajax({
            url: actionUrl,
            method: method,
            data: formData
        })
            .done((data) => {

                // Affichage de message de progression de traitement
                const traitement_token = data.data?.traitement_token;
                if (traitement_token) {
                    this.pollTraitementStatus(traitement_token, () => {
                         NotificationHandler.show(data.type, data.title, data.message);
                    });
                }else{
                     NotificationHandler.show(data.type, data.title, data.message);
                }


                this.tableUI.indexUI.formUI.loader.hide();
               
                this.tableUI.indexUI.modalUI.close();
                if (typeof onSuccess === 'function') {
                    onSuccess();
                }
                this.tableUI.loadListAction.loadEntities();
            })
            .fail((xhr) => {
                this.tableUI.indexUI.formUI.loader.hide();

                if (xhr.responseJSON?.errors) {
                    this.tableUI.indexUI.formUI.showFieldErrors(xhr.responseJSON.errors);
                }

                AjaxErrorHandler.handleError(xhr, "Erreur lors du traitement du formulaire.");
            });
    }

    /**
     * Rcupre les IDs slectionns dans le tableau.
     * @returns {Array} Liste des IDs slectionns.
     */
    getSelectedIds() {
        const checkboxes = document.querySelectorAll(`${this.config.crudSelector} .check-row:checked`);
        return Array.from(checkboxes).map(cb => cb.dataset.id);
    }
}