import EventUtil from './../utils/EventUtil';

export class PaginationUI {
    constructor(config, indexUI) {
        this.config = config;
        this.indexUI = indexUI; // Rfrence  `TableUI`
        this.page = 1;
    }

    /**
     * Initialise la gestion des vnements de pagination.
     */
    init() {
        this.handlePaginationClick(); // Grer les clics de pagination
    }

    /**
     * Gre les clics sur les liens de pagination.
     */
    handlePaginationClick() {
        EventUtil.bindEvent('click', this.config.paginationSelector, (e) => {
            e.preventDefault();
    
            const page = $(e.currentTarget).data('page') || $(e.target).text().trim();
    
            if (page) {
                const filters = this.indexUI.filterUI.getFormData(true); // Inclure tous les champs, mme vides
                filters.page = page; // Ajouter le numro de page
                this.page = page;
    
                // Mettre  jour l'URL avec tous les paramtres
                this.indexUI.updateURLParameters(filters);
    
                // Charger les entits avec les filtres et la page
                this.indexUI.tableUI.loadListAction.loadEntities(page, filters);
            }
        });
    }

}
