// /components et /utils existent dj dans ton projet
import { AjaxErrorHandler } from '../components/AjaxErrorHandler';
import { Action } from './Action';
import EventUtil from '../utils/EventUtil';
import { NotificationHandler } from '../components/NotificationHandler';

export class CrudAction extends Action {

  /**
   * @param {Object} config
   * @param {Object} tableUI
   * @param {string|null} containerSelector - par dfaut config.crudSelector
   */
  constructor(config, tableUI, containerSelector = null) {
    super(config);
    this.config = config;
    this.tableUI = tableUI;
    this.containerSelector = containerSelector || this.config.crudSelector;

    // Message de succs gnrique (les enfants peuvent le surcharger)
    this.successMessage = 'Opration effectue avec succs.';
  }

     /**
     * Lance le traitement diffr (sans attendre la rponse) puis dmarre le polling.
     *
     * @param {string} token - Token du traitement  surveiller
     * @param {object|null} loader - Loader avec .showNomBloquante() et .hide()
     * @param {function|null} onDoneCallback - Fonction appele aprs traitement termin
     */ 
    pollTraitementStatus(token, onDoneCallback = null) {

        let loader = this.loader_traitement;
        loader.showNomBloquante(" Traitement lanc...");
        let error = false;


        $.get('/admin/traitement/start')
        .done(() => {
        })
        .fail((xhr) => {
            //  Une erreur est survenue ct serveur
            const message = xhr.responseJSON?.message || 'Erreur lors du dmarrage du traitement.';
            NotificationHandler.showError(' ' + message);
            loader?.hide();
            error = true;
            this.tableUI.loadListAction.loadEntities();
        });


        //  Dmarrer le polling
        const poll = () => {
            $.get('/admin/traitement/status/' + token)
                .done((res) => {
                    const status = res.status;
                    const progress = res.progress ?? 0;
                    const messageError = res.messageError ?? "";
                    const label = res.label ?? " Traitement en cours";

                    if (status === 'done') {
                        if (loader) loader.hide();
                       // NotificationHandler.showSuccess(' Traitement termin.');
                        this.tableUI.loadListAction.loadEntities();
                        if (typeof onDoneCallback === 'function') {
                            onDoneCallback();
                        }

                    } else if (status.startsWith('error')) {
                        if (loader) loader.hide();
                        NotificationHandler.showError(' Erreur traitement : ' + messageError);
                        this.tableUI.loadListAction.loadEntities();
                    } else {
                        if(!error){
                            loader?.showNomBloquante(`${label} ${progress}%`);
                            setTimeout(poll, 1500);
                        }
                    }
                })
                .fail(() => {
                    if (loader) loader.hide();
                    NotificationHandler.showError(' Erreur rseau pendant le polling.');
                    this.tableUI.loadListAction.loadEntities();
                });
        };

        poll(); //  Lancer immdiatement la boucle de polling
    }

}