export class SessionStateService {
    /**
     * Constructeur pour initialiser SessionStateService.
     * @param {Object} initialState - L'tat initial de la session.
     * @param {String} prefix - Prfixe pour les paramtres.
     */
    constructor(sessionState = {}, prefix = 'session_') {
        this.prefix = prefix; // Prfixe pour les paramtres de session.
        this.init();
    }

    init(){
        this.sessionState = window.sessionState ; 
    }

    /**
     * Rcupre toutes les variables de session.
     * @returns {Object} - Les variables de session.
     */
    getVariables() {
        return this.sessionState.session_data;
    }

    getValue(key){
       return  this.sessionState.session_data[key];
    }

    /**
     * Met  jour l'tat de la session.
     * @param {Object} newState - Les nouvelles variables  ajouter ou remplacer.
     */
    updateSession(newState) {
        this.sessionState = { ...this.sessionState, ...newState };
    }

    /**
     * Rcupre les paramtres de session sous forme prfixe.
     * @returns {String} - Paramtres avec prfixe sous forme de chane.
     */
    getSessionParams() {
        const prefixedSession = {};
        Object.entries(this.sessionState.session_data).forEach(([key, value]) => {
            prefixedSession[`${this.prefix}${key}`] = value;
        });
        return new URLSearchParams(prefixedSession).toString();
    }

    /**
     * Ajoute l'tat de session  l'objet config, y compris dans les URLs, et le retourne.
     * @param {Object} config - L'objet de configuration  modifier.
     * @returns {Object} - L'objet de configuration avec les paramtres de session ajouts.
     */
    addSessionToConfig(config) {
        // Clone l'objet de configuration pour viter les modifications directes
        const updatedConfig = { ...config };

        // Prparer les paramtres de session sous forme de chane
        let sessionParams;
        const prefixedSession = {};
        Object.entries(this.sessionState.session_data).forEach(([key, value]) => {
            prefixedSession[`${this.prefix}${key}`] = value;
        });
        sessionParams = new URLSearchParams(prefixedSession).toString();

        // Ajouter les paramtres de session aux URLs
        Object.keys(updatedConfig).forEach((key) => {
            if (key.toLowerCase().endsWith('url') && typeof updatedConfig[key] === 'string') {
                const url = new URL(updatedConfig[key], window.location.origin);
                const separator = url.search ? '&' : '?';
                updatedConfig[key] = `${url.toString()}${separator}${sessionParams}`;
            }
        });

        return updatedConfig;
    }
}
