export default class EventUtil {
    /**
     * Supprime l'vnement existant avant de l'ajouter pour viter les doublons.
     * @param {string} eventType - Type d'vnement (ex: 'click', 'input', 'submit').
     * @param {string} selector - Slecteur jQuery cible.
     * @param {Function} callback - Fonction callback excute lors de l'vnement.
     */
    static bindEvent(eventType, selector, callback) {
        $(document).off(eventType, selector).on(eventType, selector, callback);
    }
}
